@charset "UTF-8";
mark {
  background-color: #ff6; }

input:focus, button:focus {
  outline: 0; }

.container {
  max-width: 100%; }

.infowindow {
  width: 234px;
  max-height: 250px;
  padding-bottom: 5px; }
  .infowindow h3 {
    margin: 0 0 15px 0;
    font-size: 24px; }
  .infowindow h5 {
    margin: 15px 0 0 0;
    font-size: 14px;
    font-weight: bold; }
  .infowindow address {
    font-style: normal;
    margin: 0 0 15px 0; }
  .infowindow a {
    color: inherit; }
    .infowindow a.btn-primary {
      color: #fff; }
    .infowindow a small::before {
      content: " / "; }
  .infowindow p {
    margin-bottom: 15px; }
    .infowindow p:last-child {
      margin-bottom: 0; }
  .infowindow dl {
    overflow: auto;
    margin: 0; }
    .infowindow dl dt, .infowindow dl dd {
      float: left;
      margin: 0;
      padding: 0; }
    .infowindow dl dt {
      color: #aaaaaa;
      font-weight: normal;
      width: 62px;
      clear: left;
      margin-right: 6px;
      text-align: right;
      overflow: hidden;
      text-overflow: ellipsis;
      word-wrap: nowrap;
      font-size: 12px; }
    .infowindow dl dd {
      width: 166px; }

/* meetings */
#meetings {
  min-height: 550px;
  padding: 0 15px;
  clear: both; }
  #meetings .controls {
    min-height: 34px;
    margin-bottom: 5px; }
    #meetings .controls form, #meetings .controls .dropdown, #meetings .controls .btn-group {
      margin-bottom: 15px;
      height: 34px; }
    #meetings .controls input[name=query] {
      margin: 0; }
    #meetings .controls input, #meetings .controls .btn {
      height: 34px;
      padding: 6px 12px; }
    #meetings .controls ul {
      min-width: 100%; }
      #meetings .controls ul.children {
        padding: 0;
        list-style-type: none; }
        #meetings .controls ul.children li a {
          display: block;
          padding: 3px 20px 3px 35px;
          clear: both;
          font-weight: 400;
          line-height: 1.42857143;
          color: #333;
          white-space: nowrap; }
          #meetings .controls ul.children li a::before {
            content: "â€¢";
            position: absolute;
            left: 25px; }
          #meetings .controls ul.children li a:hover {
            color: #262626;
            text-decoration: none;
            background-color: #f5f5f5; }
        #meetings .controls ul.children li.active > a {
          color: #fff;
          background-color: #337ab7; }
    #meetings .controls a {
      overflow: hidden; }
  #meetings .results h1 small {
    font-size: inherit; }
    #meetings .results h1 small::before {
      content: " / "; }
  #meetings .results #table-wrapper {
    margin: 0 -15px;
    overflow: hidden; }
  #meetings .results #table-wrapper table {
    margin: 0; }
  #meetings .results table {
    display: none; }
    #meetings .results table thead {
      display: none; }
    #meetings .results table tbody tr {
      display: block;
      padding: 10px 15px 10px 90px;
      border-top: 1px solid #ddd;
      position: relative; }
      #meetings .results table tbody tr td {
        display: inline;
        padding: 0;
        border: 0;
        overflow: hidden; }
        #meetings .results table tbody tr td.time {
          position: absolute;
          left: 15px; }
          #meetings .results table tbody tr td.time span {
            display: block;
            font-size: 12px;
            margin-top: 2px; }
            #meetings .results table tbody tr td.time span:last-child {
              font-size: inherit;
              margin-top: 0; }
        #meetings .results table tbody tr td.name, #meetings .results table tbody tr td.location, #meetings .results table tbody tr td.address, #meetings .results table tbody tr td.region {
          display: block;
          text-overflow: ellipsis;
          white-space: nowrap; }
        #meetings .results table tbody tr td.name {
          width: 175px; }
          #meetings .results table tbody tr td.name small {
            position: absolute;
            right: 15px;
            color: #333;
            font-size: 11px;
            top: 13px; }
        #meetings .results table tbody tr td.location, #meetings .results table tbody tr td.address, #meetings .results table tbody tr td.region {
          width: 220px; }
  #meetings .results #map {
    height: 550px;
    display: none;
    margin: 0 0 15px; }
  #meetings[data-type=map] #map {
    display: block; }
  #meetings[data-type=map] #map_options {
    display: block; }
  #meetings[data-type=list] table {
    display: table; }
  #meetings[data-type=list] #map_options {
    display: none; }

#meeting, #location {
  margin-bottom: 30px; }
  #meeting .page-header, #location .page-header {
    background-color: transparent; }
  #meeting h1, #location h1 {
    font-size: 36px;
    margin-bottom: 4px; }
  #meeting dl, #meeting dt, #meeting dd, #location dl, #location dt, #location dd {
    margin: 0; }
  #meeting dd, #location dd {
    margin-bottom: 20px; }
  #meeting ul, #location ul {
    padding: 0;
    margin: 0;
    list-style: none; }
  #meeting small, #location small {
    color: #333; }
    #meeting small::before, #location small::before {
      content: " / "; }
  #meeting #map, #location #map {
    margin-bottom: 15px; }

#meeting #feedback form, #meeting #feedback .alert {
  display: none; }

#meeting #feedback textarea {
  height: 250px; }

#meeting #feedback form {
  margin-bottom: 15px; }

#meeting #feedback.form form {
  display: block; }

#meeting #feedback.form dt, #meeting #feedback.form dd {
  display: none; }

#meeting #feedback.confirm .alert {
  display: block; }

#meeting #feedback.confirm dt, #meeting #feedback.confirm dd {
  display: none; }

body.tsml_fullscreen #meetings {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  padding: 0 15px;
  background-color: #fdfdfc;
  max-width: none; }

body.tsml_fullscreen.admin-bar #meetings {
  padding-top: 47px; }

/* Small devices (tablets, 768px and up) */
@media (min-width: 768px) {
  #meetings .controls {
    margin-bottom: 0; }
    #meetings .controls ul.dropdown-menu {
      max-height: 310px;
      overflow: auto; }
  #meetings .results table {
    margin: 0; }
    #meetings .results table thead {
      display: table-header-group; }
      #meetings .results table thead th {
        line-height: 1;
        padding-top: 0;
        text-transform: none;
        cursor: pointer;
        position: relative;
        padding: 8px 8px 8px 15px; }
        #meetings .results table thead th[data-sort="asc"]:after, #meetings .results table thead th[data-sort="desc"]:after {
          content: "";
          border-style: solid;
          width: 0;
          height: 0;
          position: absolute;
          right: 10px;
          top: 12px; }
        #meetings .results table thead th[data-sort="asc"]:after {
          border-width: 0 4px 8px;
          border-color: transparent transparent #aaa transparent; }
        #meetings .results table thead th[data-sort="desc"]:after {
          border-width: 8px 4px 0;
          border-color: #aaa transparent transparent; }
        #meetings .results table thead th.address {
          display: none; }
    #meetings .results table tbody tr {
      display: table-row; }
      #meetings .results table tbody tr td.name, #meetings .results table tbody tr td.location, #meetings .results table tbody tr td.time, #meetings .results table tbody tr td.address, #meetings .results table tbody tr td.region {
        white-space: normal;
        display: table-cell;
        padding: 8px 8px 8px 15px;
        position: static; }
      #meetings .results table tbody tr td.time {
        white-space: nowrap; }
        #meetings .results table tbody tr td.time span {
          display: inline;
          font-size: inherit; }
          #meetings .results table tbody tr td.time span:after {
            content: ", "; }
          #meetings .results table tbody tr td.time span:last-child:after {
            content: " "; }
      #meetings .results table tbody tr td.name {
        width: 35%; }
        #meetings .results table tbody tr td.name small {
          position: static; }
          #meetings .results table tbody tr td.name small:before {
            content: " / "; }
      #meetings .results table tbody tr td.location {
        width: 35%; }
      #meetings .results table tbody tr td.address {
        display: none; } }

/* Medium devices (desktops, 992px and up) */
@media (min-width: 992px) {
  #meetings .results table thead tr th.address, #meetings .results table tbody tr td.address {
    display: table-cell; }
  #meetings .results table thead tr th.name, #meetings .results table tbody tr td.name {
    width: 25%; }
  #meetings .results table thead tr th.location, #meetings .results table tbody tr td.location {
    width: 25%; } }

/* Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  #meetings .results table .address {
    display: table-cell; } }

@media print {
  a[href]:after {
    content: none !important;
    /* Remove Bootstrap's (http://) when printing */ }
  #meetings .results table td.name, #meetings .results table td.location, #meetings .results table td.address, #meetings .results table td.region {
    width: auto !important;
    white-space: normal; }
  #meetings .results table td.name {
    font-weight: bold; }
    #meetings .results table td.name small {
      display: none; } }

/* Special per-theme fixes */
body.divi div#et-top-navigation ul#top-menu li a {
  padding-left: 0;
  padding-right: 0;
  padding-top: 0; }
  body.divi div#et-top-navigation ul#top-menu li a:hover {
    background-color: transparent; }

body.divi.tsml_fullscreen header#main-header {
  position: static; }

body.divi.tsml_fullscreen .fbanner {
  position: static; }

body.twentyfourteen #page::before {
  display: none; }

body.twentyfourteen #meetings {
  width: 100%;
  padding-top: 20px; }
  body.twentyfourteen #meetings[data-type=list] {
    padding-bottom: 0; }
  body.twentyfourteen #meetings table {
    border: 0; }
    body.twentyfourteen #meetings table td, body.twentyfourteen #meetings table th {
      border-right: 0; }

body.twentyfourteen #meeting .page-header, body.twentyfourteen #location .page-header {
  margin: 0 0 15px;
  padding: 0 0 15px;
  max-width: none; }
